import React, { useState } from 'react';
import { IonCol, IonRow } from '@ionic/react';
import './Toggle.scss';

const Toggle = (props: any) => {
    const tabNames = props.data;
    const colsize = (12/tabNames.length).toString();
    const [showActive, setShowActive] = useState(true);
    const handleClick=(e:any)=>{
       setShowActive(false);
    }
    const tab1 = tabNames[0]
    const tabs = tabNames.slice(1,tabNames.length);
    return (
        <div className="header_toggle_div">
            <IonRow class="header_toggle_layer">
                <IonCol size = {colsize}>
                    <button  onClick={(e)=>handleClick(e)} className={showActive ?"header_toggle_layer_button":""}>{tab1} </button>
                </IonCol>
                {
                   tabs.map((tab:any, index:any) => (
                        <IonCol size = {colsize} key={index}>
                            <button  onClick={(e)=>handleClick(e)} className={`${tab === 'Liked' ?"header_tab_like":""}`}>{tab} </button>
                        </IonCol>
                    ))
                }
            </IonRow>
        </div>
    );
};

export default Toggle;