

import React, { Component } from 'react';
import { Route,Redirect } from 'react-router-dom';
import './search.page.scss'
import {
  IonIcon,
  IonLabel,
  IonRouterOutlet,
  IonTabBar,
  IonTabButton,
  IonTabs,
   IonContent,
  IonPage
 
} from '@ionic/react';

import {NewContainer} from '../../containers/NewContainer'
import {RecentContainer} from '../../containers/RecentContainer'
import { arrowBack } from 'ionicons/icons';
import { FavouriteContainer } from 'app/containers/FavouriteContainer';
import {TabHeader} from '../../components/MainTab/tab-header'

const LoadsList = ["New", "Recent", "Favorite"];

class SearchPage extends Component<any,any> {
	constructor (props:any) {
		super(props);
	
	   this.state = {
	
		tab:false,
		toggleBtn:true
	  };
	  }
	
	render() {
		return (
		 <IonPage>
	
	
			  
			  <IonContent>
			  <TabHeader  Title="New Search"  LoadsList={LoadsList}  tab={this.state.tab} toggleBtn={this.state.toggleBtn} />
         <IonTabs>
				<IonRouterOutlet>
            <Route path="/search" render={() => <Redirect to="/search/searchForm" />} exact={true} />
            <Route path="/search/:tab(searchForm)" component={NewContainer}  />
				  <Route path="/search/:tab(RecentSearch)" component={RecentContainer}  />
				  <Route path="/search/:tab(favouriteSearch)" component={FavouriteContainer} />
			
				</IonRouterOutlet>
				<IonTabBar slot="top">
				  <IonTabButton tab="searchForm" href="/search/searchForm">
					<IonLabel>New</IonLabel>
				  </IonTabButton>
				  <IonTabButton tab="RecentSearch" href="/search/RecentSearch">
					<IonLabel>Recent</IonLabel>
				  </IonTabButton>
				  <IonTabButton tab="favouriteSearch" href="/search/favouriteSearch">
					<IonLabel>Favourite</IonLabel>
				  </IonTabButton>
				</IonTabBar>
			  </IonTabs>
			
			  </IonContent>
     </IonPage>
		
		
		);
	}
}
export default SearchPage;