import { IonRow, IonCol, IonBadge } from '@ionic/react';
import * as React from "react";

const FilterBadge = (props: any) => {
  const filterlist = props.filterList;
  return (
    
      filterlist.map((filter: string, index: number) => (
        <IonBadge key={index} class="card_filter_badge">{filter} </IonBadge>
      ))
    
    
  )
}

class LoadTileFooter extends React.Component<any, any> {
 // eslint-disable-next-line
  constructor(props: any) {
    super(props);
  }
   filterList = ["1 Stop-off","Dry Van","2200 miles"];
  public render() {
    return (
      <IonRow className="loadCardFooter">
        <IonCol size="9" >
          <FilterBadge  filterList={this.filterList}/>
        </IonCol>
        <IonCol size="3" className="card_price">{this.props.price}</IonCol>
      </IonRow>
    );
  }
}

export { LoadTileFooter };
