



import React, { Component } from 'react';

import { IonContent, IonPage, IonButton } from '@ionic/react';
import {TabHeader} from '../../components/MainTab/tab-header'


interface loadDetailsPageProps {
  logout: Function
}
class LoadDetailsPage extends Component<loadDetailsPageProps,any> {
	constructor (props:any) {
		super(props);
	
	   this.state = {
	
		tab:false,
		toggleBtn:false
	  };
	  }
	
	render() {
		return (
		  <IonPage  className="page-header">
       <TabHeader  Title="Settings"  toggleBtn={this.state.toggleBtn}  tab={this.state.tab}  />
      <IonContent className="ion-padding">
        <IonButton onClick={() => this.props.logout()}>Logout</IonButton>
      </IonContent>
    </IonPage>
		
		
		);
	}
}
export default LoadDetailsPage;
