import React, {Component} from 'react';
import { connect } from 'react-redux';
import { logout } from 'app/actions/Login/action';
import LoadDetailsPage from 'app/pages/LoadDetails/LoadDetails.page';

interface LoadDetailsProps  {
    logout: Function
}

class LoadDetailsContainer extends Component<LoadDetailsProps> {

    logout = () => {
        this.props.logout()
    }

    render() {
        return (<LoadDetailsPage logout={this.logout}/>)
    }
}

const mapStateToProps = (state:any) => ({});


const mapDispatchToProps = (dispatch:Function) => ({
    logout: () => dispatch(logout())
});

export default connect(mapStateToProps, mapDispatchToProps)(LoadDetailsContainer);