


import React from 'react';
import { IonContent, IonCard, IonCardContent, IonGrid, IonRow, IonCol, IonIcon } from '@ionic/react';
import recent from "./favorite.json";
const Card2=(props:any)=>{
	return(
	<IonCardContent>
		<IonGrid >
		<IonRow>
			<IonCol size="4" className="left"><b> {props.details2.origin} </b> <br/> <span className="cardText">{props.details2.pickUpDate}</span> <br/> <span className="cardText"> {props.details2.originRadious !== "" ? <IonIcon name="arrow-back"></IonIcon> : <p></p>}{props.details2.originRadious}</span></IonCol>
			<IonCol size="4" className="center"><IonIcon name="arrow-back"></IonIcon> <br/> <span className="cardText">{props.details2.trailerType} </span> <br/> <IonIcon name="arrow-forward"></IonIcon> </IonCol>
			<IonCol size="4" className="right"><b> {props.details2.destination} </b> <br/> <span className="cardText">{props.details2.dropDate}</span> <br/> <span className="cardText"> {props.details2.destinationRadious !== "" ? <IonIcon name="arrow-back"></IonIcon> : <p></p>}{props.details2.destinationRadious}</span></IonCol>
		</IonRow>
		</IonGrid>
  	</IonCardContent>
  )}
class FavouritePage extends React.Component<any, any>{	
	render() {
		
	  return (
	
		  <IonContent>
		
				<div>
				<p> Select one of your Favourite searches to see matching </p>
				<IonCard >
					<IonCardContent>
						<IonGrid >
							<IonRow> </IonRow>
							<IonRow>
							<IonCol> <p> All loads that I liked and still availble </p></IonCol>
							</IonRow>
						</IonGrid>
					</IonCardContent>
				</IonCard>
				<IonCard >
					<IonCardContent>
						<IonGrid >
							<IonRow> </IonRow>
							<IonRow>
							<IonCol> <p> All loads that dispatcher liked and still availble </p></IonCol>
							</IonRow>
						</IonGrid>
					</IonCardContent>
				</IonCard>
					{ 
						recent.map((detail,index)=>(
							<IonCard  key={index}>
								<Card2  details2={detail} />
							</IonCard>
						))
					}
				</div>
		
		  </IonContent>
	
	  );
	}
}

export default FavouritePage;


