import * as React from "react";

import { IonHeader, IonRow, IonCol, IonBadge, IonLabel, IonSelect, IonSelectOption


} from "@ionic/react";
import Toggle from '../toggle/Toggle';


interface State {
  value: string;
};
class TabHeader extends React.Component<any,State> {
  // eslint-disable-next-line
  constructor(props: any) {
    super(props);
    this.state={
      value:'Distance'
    }
  }
 handleChange=(e:any) => {
  this.setState({ value: e.target.value })

 }
 

  public render() {
    return (

      <IonHeader className="page-header">
        <div id="header-title">{this.props.Title}</div>
     {this.props.toggleBtn?<Toggle data={this.props.LoadsList} />:null}

        {this.props.loadSize ? (<div id="short-div">
         
         {this.props.tab?<IonRow>
           <IonCol size="4" class="rec_text">
               <b>{this.props.loadSize} </b>Recommendations
           </IonCol>
           <IonCol size="4">
             <IonBadge>
               <IonRow>
                 <IonCol size="4">
                   <IonLabel>Sort By: </IonLabel>
                 </IonCol>
                 <IonCol size="8">
                   <IonSelect placeholder="Select" interface="alert" class="badge_sortby_select" onClick={this.handleChange} value={this.state.value} >
                   <IonSelectOption value="Price">Price</IonSelectOption>
                     <IonSelectOption value="Pickup date">Pickup date</IonSelectOption>
                     <IonSelectOption value="Loaded Miles">Loaded Miles</IonSelectOption>
                     <IonSelectOption value="DeadHead">DeadHead</IonSelectOption>
                   </IonSelect>
                 </IonCol>
               </IonRow>
             </IonBadge>
           </IonCol>
           <IonCol size="4">
             <IonBadge class="header_badge_select">
               <IonSelect placeholder="Select" interface="alert" >
                 <IonSelectOption value="f">Context</IonSelectOption>
                 <IonSelectOption value="m">Distance</IonSelectOption>
               </IonSelect>
             </IonBadge>
           </IonCol>
         </IonRow>:null}
        </div>):''}
      </IonHeader>

    );
  }
}

export { TabHeader };
