import { IonContent,IonPage, IonList,IonLabel } from '@ionic/react';
import React, {PureComponent} from 'react';
import { LoadTile } from "app/components/app/home/Load-Tile";
import { Loads } from "app/components/app/home/Loads";
import './Home.page.scss';
import {TabHeader} from '../../components/MainTab/tab-header'
import {RecommendedError} from './RecommendedError';

const LoadsList = ["Recommended", "Liked"];
class HomePage extends PureComponent<any, any> {


   constructor (props:any) {
    super(props);
this.handleScroll = this.handleScroll.bind(this);
   this.state = {
    prevScrollpos: window.pageYOffset,
    visible: true,
    selectedValue:'',
    tab:true,
    toggleBtn:true
  };
  }
  componentDidMount() {
    //document.getElementsByTagName("ion-content")[0].addEventListener("scroll", this.handleScroll);
  }
  componentWillUnmount() {
    //document.getElementsByTagName("ion-content")[0].removeEventListener("scroll", this.handleScroll);
  }

  handleScroll() {
    const { prevScrollpos } = this.state;

    const currentScrollPos = window.pageYOffset;
    const visible = prevScrollpos > currentScrollPos;

    this.setState({
      prevScrollpos: currentScrollPos,
      visible
    });
  };


 getLoads()
 {
   this.props.getLoads();
 }


render(){
  return (
   

    <IonPage>
    <TabHeader className={!this.state.visible ? "hide-header":''} Title="Recommended Loads" loadSize={this.props.data.length} LoadsList={LoadsList}  tab={this.state.tab}
     
    toggleBtn={this.state.toggleBtn} />
    <IonContent className="ion-padding">
      
      <div>
        <Loads loads={this.props.data} >{
          ({ loads }: { loads?: any }) => {
            return <IonList>
              {loads.map((load: any, index:number) => <LoadTile key={index} {...load} />)}
            </IonList>
          }
        }
        </Loads>
      </div>
      {!this.props.data.length && <RecommendedError loads={this.getLoads.bind(this)} />}
      {/* <IonLabel>For Demo</IonLabel>
    <RecommendedError /> */}
    </IonContent>
    
    </IonPage>

  );
        }
};

export default HomePage;
